import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';


const DEFAULT_AMOUNT = 28000 + 46400
const TOKENS = {
  9490820: 'KEY',
  50497: 'AE',
  49653: 'BTC',
  49681: 'SNT',
  3756934: 'LEND',
  1712771: 'FUEL',
  8520497: 'MTX',
  6771938: 'DEW'
}

class App extends Component {

  state = {
    isLoading: false,
    priceList: {},
    records: []
  }

  componentDidMount() {
    const  _this = this
    this.fetchExchangeRecords().then(res => {
      const data = res.data
      _this.setState({
        records: data
      })
      _this.syncTicker()
    })
  }

  syncTicker() {
    const _this = this
    const ws = new WebSocket('wss://ws.cn-east.mytokenapi.com/ticker')
    ws.onmessage = function(e) {
      const data = JSON.parse(e.data)
      const info = data.data
      if (info && info.tick) {
        const priceList = _this.state.priceList
        priceList[info.tick.symbol] = info.tick.price
        _this.setState({
          priceList
        })
      }
    }
    ws.onopen = function() {
      ws.send(
        JSON.stringify({"event":"ticker","params":{"ids": Object.keys(TOKENS).map(id => +id)}})
      )
    }
  }

  fetchExchangeRecords() {
    return fetch('https://www.easy-mock.com/mock/59d892fd9d342f449f303370/app/records').then(res => res.json())
  }

  render() {
    const { priceList, records } = this.state
    const rows = records.map((data, index) => (<tr key={index}><td>{data[0]}</td><td>{data[1]}</td><td>{data[2]}</td><td>{data[3]}</td></tr>))
    let remain = 0
    const assetsExchanged = {}
    records.forEach(data => {
      remain += parseInt(data[1])

      // 如果有兑换记录
      if (data[2]) {
        const res = data[2].split(/\d+/)
        //const isAdd = res[0] === '+'
        const tokenName = res[1]
        const amount = parseInt(data[2])
        if (typeof assetsExchanged[tokenName] === 'undefined') {
          assetsExchanged[tokenName] = amount
        } else {
          assetsExchanged[tokenName] = assetsExchanged[tokenName] + amount
        }
        if (assetsExchanged[tokenName] === 0) {
          //delete assetsExchanged[tokenName]
        }
      }
    })

    let exchangedValue = 0
    let priceSyncCount = 0
    let needPrice = 0
    Object.keys(assetsExchanged).forEach(tokenSymbol => {
      if (assetsExchanged[tokenSymbol] > 0) {
        needPrice +=1
        if (this.state.priceList[tokenSymbol]) {
          exchangedValue += assetsExchanged[tokenSymbol] * this.state.priceList[tokenSymbol]
          priceSyncCount += 1
        }
      }
    })

    
    const priceSyncOK = priceSyncCount === needPrice
    const canShowPrice = priceList.AE && priceSyncOK
    //const btc = priceList.BTC && canShowPrice ? (exchangedValue + remain * priceList.AE)/priceList.BTC : ''

    window.assetsExchanged = assetsExchanged
    
    return (
      <div className="App">
        <h2>兑换记录</h2>
        <table border="0" cellPadding="0" cellSpacing="0" width="88%" align="center">
          <thead>
            <tr>
              <th width="20%">DATE</th>
              <th width="25%">SELL/BUY AE</th>
              <th width="25%">SELL/BUY TOKEN</th>
              <th width="30%">NOTE</th>
            </tr>
          </thead>
          <tbody>
            { rows }
            <tr>
              <td>BENEFIT</td>
              <td className="ae-total">REMAIN <b>{remain}</b> AE</td>
              <td>CONVERT <b>{canShowPrice ? parseInt(exchangedValue/priceList.AE) : <Loading />}</b> AE</td>
              <td>TOTAL <b>{canShowPrice ? 
                (remain + parseInt(exchangedValue/priceList.AE)) + ' (' + (remain + parseInt(exchangedValue/this.state.priceList.AE) - DEFAULT_AMOUNT) + ')'
                :
                 <Loading />
              }</b> AE</td>
            </tr>
          </tbody>
        </table>

        <h2>已兑换资产</h2>
        <table border="0" cellPadding="0" cellSpacing="0" width="88%" align="center" className="exchangedAssets">
            <thead>
              <tr>
                <th width="20%">TOKEN</th>
                <th width="25%">PRICE</th>
                <th width="65%">AMOUNT</th>
              </tr>
            </thead>
            <tbody>
              {
                Object.keys(assetsExchanged).map(token => <tr key={token}><td>{token}</td><td>{ priceList[token] ? '￥' + priceList[token].toFixed(5) : <Loading /> }</td><td>{assetsExchanged[token]}</td></tr>)
              }
            </tbody>
        </table>
      </div>
    );
  }
}


const Loading = () => (
  <div className="loadEffect">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
  </div>
)

export default App;
